.dropdown{
    height: 40px;
    position: relative;
    width: 180px;
    z-index: 1000;
}

.dropdown > .displayer{
    border: 1px solid rgba(0,0,0,0.15);
    border-radius: 4px;
    height: 100%;
    display: flex;
    width: 100%;
}

.dropdown > .displayer > .label{
    flex: 4;
    line-height: 40px;
    padding: 0 15px;
    text-align: left;
}

.dropdown > .displayer > .icon{
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: center;
}

.dropdown > .displayer > .icon > img{
    max-width: 30%;
}

.dropdown > .option-list{
    background: white;
    border: 1px solid rgba(0,0,0,0.15);
    border-radius: 4px;
    display: none;
    height: 180px;
    overflow: hidden;
    position: absolute;
    width: 100%;
}

.dropdown > .option-list.show{
    display: block;
}

.dropdown > .option-list > .scroller {
    height: 100%;
    overflow-y: auto;
    width: 100%;
}

.dropdown > .option-list > .scroller > .option{
    padding: 10px 15px;
    text-align: left;
}

.dropdown > .option-list > .scroller > .option:hover{
    background: orangered;
    color: white;
    cursor: pointer;
}