@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Regular.ttf");
    font-weight: 400;
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-SemiBold.ttf");
    font-weight: 600;
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
    font-weight: 500;
}

.product-list-container{
    max-width: 80%;
    margin: 0px auto;
    margin-bottom: 15px;
    padding: 15px;
}

.product-list-container > h2 {
    font-family: AlumniSans;
    font-size: 36px;
    letter-spacing: 0.5px;
    margin: 25px;
    margin-top: 10px;
    /* text-transform: uppercase; */
}

.product-list-container > hr{
    opacity: 0.4;
    width: 120px;
}

.product-list-container > .link{
    background: orangered;
    border-radius: 30px;
    color: white;
    display: block;
    font-family: AlumniSans;
    font-size: 18px;
    margin: 30px auto;
    padding: 5px 0px;
    text-decoration: none;
    width: 120px;
}

.product-list-container > .link:visited{
    color: white;
}

.product-list-container > .product-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.product-item-container{
    margin: 10px 20px;
    position: relative;
    width: calc(100%/4.65);
    
}

.product-item{
    background-color: white;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.07);
    overflow: hidden;
    padding: 15px;
    position: relative;
    width: 100%;
}

.product-item-container div{
    user-select: none;
}

.product-thumbnail{
    background: white;
    border-radius: 8px;
    height: 150px;
    max-width: 100%;
    overflow: hidden;
    position: relative;
}

.product-thumbnail > img{
    height: 100%;
    left: 50%;
    position: absolute;
    top:50%;
    transform: translate(-50%,-50%);
}

.product-name {
    color: black;
    font-family: AlumniSans;
    font-size: 20px;
    font-weight: 500;
    /* text-transform: uppercase; */
    margin: 5px;
    margin-top: 15px;
    text-align: left;
}

.product-notify{
    background: orangered;
    border-radius: 4px;
    border-top-left-radius: 0px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 0px;
    bottom:0;
    color: white;
    font-family: AlumniSans;
    font-size: 14px;
    position: absolute;
    text-align: left;
    
} 

.product-price{
    /* background: #F27023; */
    border-radius: 8px;
    color: black;
    font-family: AlumniSans;
    font-size: 22px;
    letter-spacing: 0.5px;
    margin: 5px;
    padding: 5px 0;
    /* text-transform: uppercase; */
    text-align: left;
}

.product-description{
    color: rgba(0,0,0,.4);
    font-size: 16px;
    font-family: AlumniSans;
    margin: 5px;
    max-width: 250px;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left;
    white-space: nowrap;
}
