@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}

.carousel{
    height: 300px;
    margin-top: 30px;
    width: 100%;
}

.carousel > .container{
    display: flex;
    height: 250px;
    margin: 0 auto;
    width: 80%;
}

.carousel > .indicators{
    align-items: center;
    display: flex;
    height: 15px;
    justify-content: center;
    margin: 40px auto;
    width: 60px;
}

.carousel > .indicators > .indicator{
    border-radius: 100px;
    background: silver;
    height: 8px;
    margin: 0 auto;
    transform:scale(1);
    transition: transform 0.2s ease-in-out;
    width: 8px;
}

.carousel > .indicators > .indicator.active{
    background: orangered;
    transform:scale(1.3);
    transition: transform 0.2s ease-in-out;
}

.carousel > .container > .slide{
    box-sizing: border-box;
    border-radius: 16px;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
    transition: transform 0.2s ease-in-out;
    width: calc(100%/3);
   
}

.carousel > .container > .slide.active{
    border-radius: 16px !important;
    box-shadow: 0px 0px 12px rgba(0,0,0,.15);
    transform: scale(1.1);
    transition: transform 0.2s ease-in-out;
    z-index: 10;
}

.carousel > .container > .slide:nth-child(1){
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.carousel > .container > .slide:nth-child(2){
    border-radius: 0;
}

.carousel > .container > .slide:nth-child(3){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.carousel > .container > .slide > .container{
    height: 100%;
    position: relative;
    width: 100%;
}

.carousel > .container > .slide  img.horizontal{
    left: 0;
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    width: 100%;
}

.carousel > .container > .slide  img.vertical{
    height: 100%;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
}