.button-group{
    display: flex;
    flex: 1;
    position: relative;
    text-align: center;
}

.quantity-button-group{
    /* align-items: center;
    display:flex;
    justify-content: center; */
    flex: 3;
    max-width: 135px;
    padding: 10px 0px; 
    /* padding-bottom: 0; */
    position: relative;
   
}

.quantity-button-group > .button{
    background-color:white;
    border: 1px solid rgba(0,0,0,.075);
    border-radius: 4px;
    box-shadow: 0px 0px 12px rgba(0,0,0,.025);
    color: orangered;
    font-size: 20px;
    font-weight: bold;
    float: left;
    height:  35px;
    line-height: 30px;
    position: relative;
    width: 35px;
}

.quantity-button-group > .button.hide{
    opacity: 0;
}

.quantity-button-group > .button > img{
    left: 50%;
    max-height: 30%;
    position: absolute;
    transform: translate(-50%,-50%);
    top:50%;
}


.quantity-button-group > .button:hover{
    cursor: pointer;
}

.quantity-button-group > .quantity-displayer {
    float: left;
    line-height: 35px;
    width: 30%
}

.quantity-button-group > .quantity-displayer > div:first-child{
    font-size: 20px;
    font-family: AlumniSans;
}

.add-to-cart-holder{
    flex: 1;
    padding: 10px 0px;
    padding-bottom: 0;
    width: 100%;
}

.add-to-cart{
    background-color:orangered; 
    border-radius: 35px;
    color:white;
    float: right;
    height: 35px;
    text-align: center;
    position: relative;
    width: 35px;
}

.add-to-cart.hide{
    opacity: 0;
}

.add-to-cart:hover{
    background: orange;
    cursor: pointer;
}

.add-to-cart > img{
    left: 50%;
    max-height: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    top:50%;
}