@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}


@font-face {
    font-family: "AlumniSans";
    font-weight: 600;
    src: url("../fonts/static/AlumniSans-Bold.ttf");
}


.footer{
    background-color: white;
    border-top: 1px solid rgba(0,0,0,.05);
    box-shadow: 0px 0px 25px rgba(70, 54, 54, 0.05);
    font-family: Questrial;
    margin-top: 30px;
    padding: 30px 0px;
}

.footer > .container {
    display: flex;
    margin: 0 auto;
    width: 80%;
}

.footer > .container > div{
    box-sizing: border-box;
    flex:1;
    padding: 0 25px;
    text-align: left;
}

.footer > .container > div:not(:nth-child(1)){
    padding-top: 30px;
}

.footer > .container > div > img{
    max-width: 60%;
}

.footer > .container > div > p{
    font-family: AlumniSans;
    font-size: 18px;
    margin: 10px 0px;
}

.footer > .container > div > p > span{
    font-weight: 600;
}

.footer > .container > div > .link{
    color: black;
    display: block;
    font-family: AlumniSans;
    margin: 10px 0;
    text-decoration: none;
}

.footer > .container > div > .link:visited{
    color: black;
}

