@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}


@font-face {
    font-family: "AlumniSans";
    font-weight: 600;
    src: url("../fonts/static/AlumniSans-Bold.ttf");
}

.photocopy-section > h2 {
    font-family: AlumniSans;
    font-size: 36px;
    letter-spacing: 0.5px;
    margin: 25px;
    margin-top: 10px;
    /* text-transform: uppercase; */
}

.photocopy-section > .container{
    background: white;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.05);
    font-family: Questrial;
    margin: auto;
    overflow: hidden;
    width: 80%;
}


.photocopy-section > .container > .thumbnail{
    background: white;
    height: 200px;
    overflow: hidden;
    position: relative;
    width: 100%;
}
.photocopy-section > .container > .thumbnail > img{
    left: 50%;
    position: absolute;
    top:50%;
    transform: translate(-50%,-50%);
    width: 100%;
}

.photocopy-section > .container > .content{
    box-sizing: border-box;
    padding: 30px;
    text-align: left;
    width: 100%;
}

.photocopy-section > .container > .content > h3{
    font-family: AlumniSans;
    font-size: 28px;
    margin: 0;
}

.photocopy-section > .container > .content p{
    font-size: 16px;
    letter-spacing: 0.5px;
    margin: 5px 0px;
}

.photocopy-section > .container > .content > p{
    font-family: AlumniSans;
    font-size: 18px;
    margin: 15px 0px;
}

.photocopy-section > .container > .content > .choice > p{
    font-family: AlumniSans;
    font-size: 18px;
    font-weight: 600;
}

.photocopy-section > .container > .content > .choice{
    margin: 25px 0px;
}

.photocopy-section > .container > .content > .choice > .choices{
    display: flex;
    flex-wrap: wrap;
    margin: 15px 0px;
}

.photocopy-section > .container > .content > .choice > .choices > .option {
    display: flex;
    width: calc(100%/4);
}

.photocopy-section > .container > .content > .choice > .choices > .option:hover{
    cursor: pointer;
} 

.photocopy-section > .container > .content > .choice > .choices > .option > .radio{
    border:1px solid rgba(0,0,0,0.15);
    border-radius: 100px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.05);
    height: 20px;
    margin-right: 15px;
    margin-top: 5px;
    position: relative;
    width: 20px;
}

.photocopy-section > .container > .content > .choice > .choices > .option > .radio > .circle{
    background: orangered;
    border-radius: 100%;
    display: none;
    height: 10px;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    width: 10px;
}

.photocopy-section > .container > .content > .choice > .choices > .option > .radio > .circle.selected{
    display: block;
}

.photocopy-section > .container > .content > .choice > .choices > .option > .label >p:first-child{
    font-size: 16px;
}

.photocopy-section > .container > .content > .choice > .choices > .option > .label >p:last-child{
    font-family: AlumniSans;
    color: rgba(0,0,0,0.7);
    font-size: 16px;
    margin-top: -4px;
}

.photocopy-section > .container > .content > hr{
    opacity: 0.4;
    margin-top: 50px;
    width: 120px;
}

.photocopy-section > .container > .content > .register-button{
    background: orangered;
    border-radius: 100px;
    color: white;
    font-family: AlumniSans;
    font-size: 18px;
    margin: 25px auto;
    padding: 5px 0;
    text-align: center;
    width: 120px;
}

.photocopy-section > .container > .content > .choice > .link-input{
    font-family: AlumniSans;
    padding: 15px 35px;
}

.photocopy-section > .container > .content > .choice > .link-input > p{
    margin-top: 15px;
    margin-bottom: 30px;
}

.photocopy-section > .container > .content > .choice > .link-input > .container{
    display: flex;
}

.photocopy-section > .container > .content > .choice > .link-input > .container > .document-link{
    flex: 10;
    padding-left: 15px;
}

.photocopy-section > .container > .content > .choice > .link-input > .container > .document-link > input{
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 4px;
    box-sizing: border-box;
    height: 45px;
    padding: 0 15px;
    width: 100%;
}

.photocopy-section > .container > .content > .choice > .link-input > .container > .document-link > p{
    font-size: 16px;
    font-weight: 600;
}

.photocopy-section > .container > .content > .choice > .link-input > .container > .quantity{
    flex: 1;
}

.photocopy-section > .container > .content > .choice > .link-input > .container > .quantity > input{
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 4px;
    box-sizing: border-box;
    height: 45px;
    padding: 0 15px;
    width: 100%;
}

.photocopy-section > .container > .content > .choice > .link-input > .container > .quantity > p{
    font-size: 16px;
    font-weight: 600;
}