.checkbox{
    align-items: center;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 4px;
    display: flex;
    height: 20px;
    justify-content: center;
    width: 20px;
}

.checkbox:hover{
    cursor: pointer;
}

.checkbox img{
    max-width: 60%;
}

.checkbox.checked{
    background: orangered;
    border: none;
}