@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}

.cart-list > .container{
    font-family: AlumniSans;
    margin: 0 auto;
    text-align: left;
    width: 80%;
}

.cart-list > .container h3{
    font-size: 28px;
}

.row{
    display: flex;
    margin: 15px 0;
    margin-bottom: 25px;
}

.col{
   
    flex: 1;
}

.col:nth-child(1){
    padding-top: 1em;
}


.col:nth-child(3){
    align-self: baseline;
    box-sizing: border-box;
    display: flex;
    flex: 4;
    justify-content: flex-end;
    padding-left: 20px;
}

.col.col-2{
    display: flex;
    flex: 10;
}

.cart-list > .container .cart-section {
    background-color: white;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.07);
    display: flex;
    margin-bottom: 10px;
    padding: 30px;
}

.cart-list > .container .cart-section  hr{
    margin-top: 0;
    opacity: 0.2;
}

.cart-list > .container .cart-section > .list > .header .col:nth-child(1){
    padding-top: 2.2em;
}

.cart-list > .container .cart-section > .list{
    flex: 2;
    max-width: 60%;
}


.cart-list > .container .cart-section > .list > .header.row{
    margin: 0;
}

.cart-list > .container .item > .col:nth-child(2){
    flex: 10;
}

.cart-list > .container .item .thumbnail{
    flex: 2;
    overflow: hidden;
} 

.cart-list > .container .item .thumbnail img{
    width: 100%;
}

.cart-list > .container .item .name-description{
    box-sizing: border-box;
    flex: 4;
    padding: 0 20px;
} 

.cart-list > .container .item .name-description h3{
    font-family: Questrial;
    font-size: 16px;
}

.cart-list > .container .item .name-description p{
    color: rgba(0,0,0,.6);
    font-family: Questrial;
    font-size: 12px;
    line-height: 16px;
}

.cart-list > .container .item .price{
    flex: 2;
    font-size: 18px;
    font-weight: 600;
    margin: 13px 0;
    text-align: center;
} 

.cart-list > .container .cart-section > .cart-section-total-info{
    box-sizing: border-box;
    flex: 1;
    padding: 0 15px;
    padding-left: 10%;
}

.cart-list > .container .cart-section > .cart-section-total-info p:nth-child(4){
    color: orangered;
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 0;
}

.cart-list > .container .cart-section > .cart-section-total-info p:nth-child(5){
    margin-top: 0;
    margin-bottom: 25px;
}

.cart-list > .container .cart-section > .cart-section-total-info p:nth-child(4) span{
    color: black;
}

.cart-list > .container .cart-section > .cart-section-total-info > .note{
    box-sizing: border-box;
    background: #f5f5f5;
    padding: 15px;
}

.cart-list > .container .cart-section > .cart-section-total-info > .note p{
    margin: 0;
}


.remove-cart-section{
    background-color:orangered; 
    border-radius: 35px;
    color:white;
    float: right;
    height: 35px;
    text-align: center;
    position: relative;
    width: 35px;
}

.remove-cart-section:hover{
    background: orange;
    cursor: pointer;
}

.remove-cart-section > img{
    left: 50%;
    max-height: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    top:50%;
}


