.radio-button{
    display: flex;
}

.radio-button > .radio{
    border:1px solid rgba(0,0,0,0.15);
    border-radius: 100px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.05);
    height: 20px;
    margin-right: 15px;
    margin-top: 5px;
    position: relative;
    width: 20px;
}

.radio-button > .radio > .circle{
    background: orangered;
    border-radius: 100%;
    display: none;
    height: 10px;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    width: 10px;
}

.radio-button > .radio > .circle.selected{
    display: block;
}
