@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}

.main-button{
    background: orangered;
    border-radius: 100px;
    color: white;
    font-family: AlumniSans;
    font-size: 18px;
    margin: 25px auto;
    padding: 5px 0;
    text-align: center;
    width: 120px;
}

.weak-button{
    background: #f2f2f2;
    border-radius: 100px;
    color: #414141;
    font-family: AlumniSans;
    font-size: 18px;
    margin: 25px auto;
    padding: 5px 0;
    text-align: center;
    width: 120px;
}

.main-button:hover, .weak-button:hover {
    cursor: pointer;
}