@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}


.student-info{
    font-family: AlumniSans ;
}

.student-info > .container{
    background: white;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.07);
    margin: 0px auto;
    margin-bottom: 10px;
    padding: 30px;
    padding-top: 10px;
    text-align: left;
    width: 80%;
}

.student-info > .container >  h3{
    font-size: 28px;
}

.student-info > .container > .info-group{
    display: flex;
}

.student-info > .container > .info-group > .info{
    display: flex;
    flex-direction: column;
    flex: 2;
    font-size: 18px;
    justify-content: center;
}

.student-info > .container > .info-group > .info > .avatar{
    border-radius: 4px;
    height: 60px;
    overflow: hidden;
    position: relative;
    width: 60px;
}


.student-info > .container > .info-group > .info > .avatar img{
    height: 100%;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
}

.student-info > .container > .info-group > .info > div:first-child{
    font-weight: 600;
}