@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}

.admin{
    display: flex;
    font-family:  AlumniSans;
    height: 100%;
    position: fixed;
    width: 100%;
}

.admin > .container{
    height: 100%;
    position: relative;
    width: 100%;
}

.admin > .container > .view{
 
    height: calc(100% - 62px);
    width: 100%;
}

.admin-view{
    height: 100%;
    max-width: 95%;
    margin: 0 auto;
}

.admin-view > .header{
    display: flex;
    width: 100%;
}

.admin-view > .header > .title{
    flex: 5;
}


.admin-view > .header > .title > h3{
    font-size: 28px;
    text-align: left;
}

.admin-view > .header > .button-group{
    align-items: flex-end;
    display: flex;
    flex: 1;
    justify-content: flex-end;
}

.admin-view > .header > .button-group > .main-button{
    margin: 25px 0;
}

.admin-view >.table{
    background-color: white;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.07);
    height: 80%;
    margin: 0 auto;
    padding: 30px;
    width: 100%;
}

.admin-view >.table hr{
    opacity: 0.2;
}

.admin-view >.table .table-row{
    display: flex;
    font-size: 18px;
   margin-top: 25px;
}

.admin-view >.table .table-data{
    height: 90%;
    overflow:hidden;
}

.admin-view >.table .table-content{
    box-sizing: border-box;
    height: 70%;
    overflow-y: auto;
    padding-top: 15px;
    width: 100%;
}

.admin-view >.table .table-row:first-child{
    margin: 0;
}

.admin-view >.table .table-row > .table-col{
    align-items: center;
    box-sizing: border-box;
    flex: 1;
    display: flex;
    text-align: left;
    padding-right: 25px;
}

.admin-view >.table .table-row > .table-col:last-child{
    /* align-items: flex-end; */
    display: flex;
    justify-content: flex-end;
}

.admin-view >.table .table-row > .table-col:last-child > .main-button, .admin-view >.table .table-row > .table-col:last-child > .weak-button{
    height: 22px;
    margin: 0 10px;
    margin-top: -5px;
    width: 80px;
}

.admin-view >.table > .table-header{
    display: flex;
    padding-bottom: 30px;
}

.admin-view >.table > .table-header > .search{
    flex: 1;
}

.admin-view >.table > .table-header > .filter-group{
    display: flex;
    justify-content: flex-end;
    flex: 1;
}

.admin-view >.table > .table-header > .filter-group > .dropdown:first-child{
    margin-right: 25px;
}

.admin-view >.table .table-col img{
    max-width: 60%;
}

.create-product .row .col{
    padding-top: 0 ;
}

.create-product .row .col .dropdown{
    height: 50px;
    margin-top: 11px;
    width:100%;
}

.create-product .row .col .dropdown .displayer .label{
    font-size: 18px;
    line-height: 50px;
}
.create-product .row .col .selection-group label{
    flex: 2;
}

