@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}

.order-list{
    width: 100%;
}

.order-tab .container{
    background-color: white;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.07);
    font-family: AlumniSans;
    margin: 0 auto;
    padding: 30px;
    text-align: left;
    width: 80%;
}

.order-tab .container h3{
    font-size: 28px;
}

.order-list{
    display: none;
}

.order-list.show{
    display: block;
}


.order-list  hr{
    opacity: 0.25;
}

.order-list .order{
    display: flex;
    margin: 25px 0;
}

.order-list .order.header{
    font-weight: 600;
}

.order-list  .order div{
    font-size: 18px;
    flex: 1;
}

.order-list .order .link{
    color: black;
    text-decoration: none;
}

.order-list .order div .main-button, .order-list .order div .weak-button{
    margin: 0;
}

.order-tabs > .tab-header{
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 8px;
    display: flex;
    overflow: hidden;
}

.order-tabs > .tab-header > div{
    flex: 1;
    font-size: 18px;
    font-weight: 600;
    margin-top: -1px;
    text-align: center;
    padding: 15px;
}

.order-tabs > .tab-header > div:nth-child(2){
    border-left: 1px solid rgba(0,0,0,0.1);
    border-right: 1px solid rgba(0,0,0,0.1);
}

.order-tabs > .tab-header > div:hover{
    background-color: #f8f8f8;
    cursor: pointer;
}

.order-tabs > .tab-header > div.active{
    background-color: orangered;
    color: white;
}