@keyframes updateCart {
    0%{transform: scale(1);}
    10%{transform: scale(1.3);}
    25%{transform: scale(0.8);}
    50%{transform: scale(1.1);}
    75%{transform: scale(0.95);}
    100%{transform: scale(1);}
}

@font-face {
    font-family: "Questrial";
    src: url("../fonts/Questrial-Regular.ttf");
}

@font-face {
    font-family: "AlumniSans";
    src: url("../fonts/static/AlumniSans-Medium.ttf");
}

.header-container{
    
    height: 60px;
    width: 100%;
}

.header-container > .header{
    background: white;
    border: 1px solid rgba(0,0,0,.05);
    box-shadow: 0px 0px 12px rgba(0,0,0,.05);
    height: 60px;
    position: fixed;
    width: 100%;
    z-index: 1000;
}

.header-container > .header > .container{
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    margin: 0 auto;
    width: 80%;
}

.header-container > .header > .container > .link{
    color: black;
    font-family: AlumniSans;
    font-size: 20px;
    flex: 1;
    margin: 0 15px;
    text-decoration: none;
}

.header-container > .header > .container > .link:visited{
    color: black
}

.header-container > .header > .container > .user-info {
    align-items: center;
    display: flex;
    flex: 2;
    justify-content: flex-end;
}

.header-container > .header > .container > .user-info > .info > p {
    margin: 0;
    text-align: left;
}

.header-container > .header > .container > .user-info > .info > p:first-child{
    font-size: 12px;
} 

.header-container > .header > .container > .user-info > .link > .cart{
    align-items: center;
    display: flex;
    height: 60px;
    justify-content: flex-end;
    position: relative;
    width: 60px;
}

.header-container > .header > .container > .user-info > .link > .cart > div{
    background: orangered;
    border-radius: 20px;
    bottom: 8px;
    color: white;
    font-family: AlumniSans;
    font-size: 14px;
    height: 20px;
    line-height: 20px;
    position: absolute;
    right: -5px;
    text-align: center;
    width: 20px;
}

.header-container > .header > .container > .user-info > .link > .cart > div.updating{
    animation: updateCart 0.6s forwards;
}

.header-container > .header > .container > .user-info > .link > .cart > img{
    max-height: 50%;
}

.user-info .sub-menu{
    background: white;
    border: 1px solid rgba(0,0,0,.05);
    box-shadow: 0px 0px 12px rgba(0,0,0,.05);
    display: none;
    height: 100px;
    position: absolute;
    top:60px;
    width: 180px;
}

.user-info:hover{
    cursor: pointer;
}

.user-info:hover .sub-menu{
    display: block;
}

.user-info:hover .sub-menu .link{
    color: black;
    display: block;
    font-family: AlumniSans;
    font-size: 18px;
    padding: 15px;
    padding-left: 40px;
    text-decoration: none;
    text-align: left;
}
.user-info:hover .sub-menu .link:nth-child(2){
    padding-top: 0;
}