.paginator{
    align-items: center;
    display: flex;
    height: 60px;
    justify-content: center;
    padding-top: 15px;
    width: 100%;
}

.paginator > .page-block{
    border: 1px solid rgba(0,0,0,0.15);
    border-radius: 4px;
    margin: 0 2px;
    padding: 5px 12px;
}

.paginator > .page-block:hover{
    cursor: pointer;
}

.paginator > .page-block.active{
    border: 1px solid rgba(0,0,0,0);
    background: orangered;
    color:white;
}