.popup{
    display: none;
    height: 100%;
    left: 0;
    position: fixed;
    top:0;
    width: 100%;
    z-index: 1000;
}

.popup.show{
    display: block;
}

.popup > .wrapper{
    height: 100%;
    position: relative;
    width: 100%;
}

.popup > .wrapper > .overlay{
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    left: 0;
    position: absolute;
    top:0;
    width: 100%;
    z-index: -1;
}

.popup > .wrapper > .container{
    background-color: white;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.075);
    border-radius : 16px;
    box-shadow: 0px 0px 25px rgba(0,0,0,0.07);
    font-family: AlumniSans;
    left: 50%;
    margin: 0 auto;
    padding: 30px;
    position: absolute;
    top: 50%;
    transform: translate(-50%,-50%);
    text-align: left;
    width: 40%;
 
}

.popup > .wrapper > .container  h3{
    font-size: 28px;
    margin-top: 0;
}

.popup > .wrapper > .container  p{
    font-size: 18px;
    margin: 7px 0;
}


.popup > .wrapper > .container  p span{
    font-weight: 600;
}

.popup > .wrapper > .container textarea{
    box-sizing: border-box;
    border:1px solid rgba(0,0,0,0.15);
    font-family: AlumniSans;
    font-size: 18px;
    height: 100px;
    margin-top: 15px;
    padding: 15px;
    resize: none;
    width: 100%;
}

.popup > .wrapper > .container .buttons{
    align-items: center;
    display: flex;
    justify-content: center;
}

.popup > .wrapper > .container .buttons div{
    margin:25px 10px;
    margin-bottom: 0;
}

.popup > .wrapper > .container .row{
    display: flex;
    margin: 0;
}

.popup > .wrapper > .container .row .image{
    border-radius:8px;
    flex: 1;
    height: 200px;
    overflow: hidden;
    position: relative;
}

.popup > .wrapper > .container .row .image img{
    height: 100%;
}

.popup > .wrapper > .container .row .content{
    flex: 1;
}

.popup .create-category input{
    border: 1px solid rgba(0,0,0,0.15);
    border-radius: 4px;
    box-sizing: border-box;
    font-family: AlumniSans;
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 15px;
    padding: 15px;
    width: 100%;
}

.popup .create-category textarea{
    margin-top: 10px !important;
    margin-bottom: 15px;
}

.popup .create-category label{
    font-weight: 600;
}
.popup .create-category .button-group{
    justify-content: center;
}

.popup .create-category .selection-group{
    align-items: center;
    display: flex;
}

.popup .create-category .selection-group > label{
    flex: 1;
}

.popup .create-category .selection-group > .option{
    flex: 5;
    display: flex;
}

.popup .create-category .selection-group > .option > .radio-button{
    margin-right: 30px;
}

